html {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  scroll-behavior: smooth;
}

body {
  padding: 0;
  margin: 0;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

.App {
  /* width: 100vw; */
  min-height: 94vh;
  /* background-color: teal; */
  max-width: 100%;
  padding: 0rem 2.5rem;
  background-color: #fdfdfd;
  /* added */
}



/*-------- Media Queries ---------*/

@media (max-width: 1000px) {

  .App {
    padding: 0rem 1rem
  }
}